body {
    display: grid;
    grid-template-columns: 15em auto 15em;
    gap: 1em;
    margin: 0;
    padding: 0;
    background-color: #FFFAFA;
    font-family: 'Montserrat', sans-serif;
}
.left {
    grid-column: 1/2;
    position: fixed;
    
}
.content-feed {
    grid-column: 2/5;
}
.right {
    grid-column: 5/6;
    
}
.logo {
    width: 40px;
    margin-left: .7em;
}
.logo img {
    width: 100%;
}
.left-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1em;
    margin-top: 1em;
}

.left-bar {
    background-color: #fff;
    box-shadow: 0px 5px 10px rgba(255, 207, 198, .3), 0px 15px 10px rgba(255, 207, 198, .1);
    padding: 1em;
    border-radius: 0 15px 15px 0;
    height: 65vh;
    position: relative;
} 
.left-bar header {
    display: flex;
    justify-content: space-between;
    align-items: center;

}
.left-bar header h4 {
    font-size: .8rem;
    
}
.left-bar header p {
    color: #FECEC1;
    font-weight: 700;
    font-size: .8rem;
}
.next-cont {
    position: absolute;
    top: 23.5em;
    left: 15%;
    width: 12.5vw;
}
.next-cont img {
    width: 100%;
}
.box-categories {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.sub-title {
    width: 5em;
    margin-right: 4.2em;
}
.box-categories p {
    font-weight: 700;
    font-size: .9rem;
}
.box-categories span {
    display: block;
    font-size: .6rem;
    font-weight: 700;
    opacity: 0.3;
    text-align: left;
}
.box-categories {
    margin-bottom: 1em;
    width: 13em;
}

.home {
    position: relative;
}
.home::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2.4px;
    top: 1.7em;
    background-color: #FECEC1;

}

.center-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: .3em;
    margin-top: .3em;
}
.center-nav h2 {
    font-size: 1.2rem;
    font-weight: 600;
}
.i-feed {
    display: flex;
    align-items: center;
}

.i-feed img {
    width: 1.5em;    
}
.i-feed button {
    background-color: #FF7B77;
    border: none;
    height: 2.5em;
    width: 9em;
    border-radius: 100px;
    color: white;
    font-weight: 600;
    margin: 0 1em 0 1em;
}
.post {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1.5em;
    margin-bottom: 1em;
}
.people-feed {
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}
.scene {
    width: 100%;
    height: auto;
    margin-top: -.8em;
    line-height: .2em;
    margin-left: 1em;
}
.scene h5 {
    font-size: 1rem;
}
.scene p {
    font-size: 1rem;
    font-weight: 500;
}
.tags {
    display: flex;
}
.tags button {
    border-radius: 100px;
    border: none;
    font-size: .9rem;
    height: 2em;
    width: 6em;
    margin-left: .4em;
    color: white;
    font-weight: 600;
}

.yellow {
    background-color: #FFA84F;
}
.green {
    background-color: #7AC133;
}
.blue {
    background-color: #7C72F9;
}
.viral {
    display: flex;
    align-items: center;
}
.border {
    border: 1px solid rgba(0,0,0,0.1);
    border-radius: 15px;
    margin-left: 1.5em; 
}
.scene {
    margin-right: 2em;
}
.scene iframe {
    border-radius: 15px;
    width: 20em;
    height: 13em;
}
.feed-reaction {
    display: flex;
    justify-content: space-between;
    padding: 1em;
}
.feedback {
    margin-left: 1em;
}
.content {
    border-radius: 15px;
    margin-bottom: 3em;
    padding: 1em;
    box-shadow: 0px 5px 4px rgba(255, 207, 198, .3), 0px 20px 6px rgba(255, 207, 198, .1);
}
.right-nav {
    padding: 1em;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.bg-pink {
    border-radius: 15px;
    background-color: #FFEDEA;
    margin: 0 1em 0 1em;
    height: auto;
    
}
.bg-white {
    background-color: white;
    box-shadow: 0px 5px 4px rgba(255, 207, 198, .3), 0px 20px 6px rgba(255, 207, 198, .1);
    border-radius: 15px;
    width: 15em;
    padding: 1em .8em;
    margin: 1.5em auto;

}
.post-popular {
    display: flex;
    align-items: center;
    gap: 1em;
}        
.post-popular h5 {
    font-size: .8rem;
}
.bg-white p {
    opacity: 0.3;
    font-size: .9rem;
}
.friend-sugestion {
    display: flex;
    flex-direction: column;
    margin-bottom: 1em;
    justify-content: space-between;
}
.friend-name {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1em;
}
.friend-name .info {
    line-height: .1em;
    display: flex;
}
.friend-name .info {
    font-weight: 600;
}
.friend-name .info h5 {
    font-size: .9rem;
}
.friend-name .info p {
    font-size: .8rem;
    opacity: 0.5;
}
.name {
    margin-left: 1em;
}
.friend-title {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.friend-title p {
    font-weight: 700;
    font-size: .9rem;
    color: #FF7B77;
}